export default {
  props: {
    item: {
      type: Object,
      default: () => {},
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      currentImage: {
        index: 0,
      },
      quantity: 1,
      isDecreaseable: false,
      totalPrice: 0,
      navigation: undefined,
    }
  },

  methods: {
    activeImage(index) {
      this.currentImage.index = index
    },
    expandImage(url) {
      this.$swal({
        showCloseButton: true,
        imageUrl: url,
        width: 1000,
        showConfirmButton: false,
        imageWidth: '100%',
        imageAlt: 'Custom image',
      })
    },
  },
}
